import { makeStyles } from "@material-ui/core";
import React, { useState } from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import SelectTextSize from "./selectTextSize";

const useStyles = makeStyles(
  {
    wrapper: {
      display: "flex",
    },
    h2: {
      color: "red",
    },
    input: {
      height: 500,
      width: 500,
      border: "1px solid black",
      textAlign: (props) => props.textAlign,
      color: (props) => props.color,
      fontSize: (props) => props.size,
    },
    center: {
      textAlign: "center",
    },
  },
  {
    name: "presentation",
  }
);

export default function CreatePresentation() {
  const [textAlign, setTextAlign] = React.useState("left");
  const [color, setColor] = React.useState("black");
  const [size, setSize] = React.useState(10);
  //console.log(size);
  const props = { textAlign: textAlign, color: color, size: size };

  const styles = useStyles(props);

  const handleChangeTextAlign = (event) => {
    setTextAlign(event.target.value);
  };
  const handleChangeColor = (event) => {
    setColor(event.target.value);
  };
  const handleChangeSize = (size) => {
    console.log(size);
    setSize(size);
  };

  return (
    <div className={styles.wrapper}>
      <h2 className={styles.h2}>presentation</h2>
      <FormControl component="fieldset">
        <FormLabel component="legend">textAlign</FormLabel>
        <RadioGroup
          aria-label="textAlign"
          name="textAlign"
          value={textAlign}
          onChange={handleChangeTextAlign}
        >
          <FormControlLabel value="left" control={<Radio />} label="Left" />
          <FormControlLabel value="center" control={<Radio />} label="Center" />
          <FormControlLabel value="right" control={<Radio />} label="Right" />
        </RadioGroup>
      </FormControl>

      <FormControl component="fieldset">
        <FormLabel component="legend">color</FormLabel>
        <RadioGroup
          aria-label="color"
          name="color"
          value={color}
          onChange={handleChangeColor}
        >
          <FormControlLabel value="black" control={<Radio />} label="Black" />
          <FormControlLabel value="red" control={<Radio />} label="Red" />
          <FormControlLabel value="blue" control={<Radio />} label="Blue" />
        </RadioGroup>
      </FormControl>
      <SelectTextSize size={size} onChange={handleChangeSize} />
      <textarea className={styles.input}></textarea>
    </div>
  );
}
